<template>
    <loader :is-visible="isLoading"></loader>
</template>
<script>
import DataService from "../../services/DataService";
export default ({
    created: function(){
        var vm =this;
        vm.SaveTokenDetail();
    },
    data(){
        return{
            isLoading: false,
        }
    },
    methods:{
        SaveTokenDetail(){
            debugger;
            var vm=this;
            this.isLoading=true;
            var code=`${vm.$route.query.code}`
             if(code =='undefined'){
                this.ShowAlert("Access Denied", "failure", this.$t('Alert'));
                this.$router.push({ name: 'AddAccount' });
                this.isLoading=false;
               
             }
             else
             {
                var params=`code=${code}&state=${vm.$route.query.state}`;
                DataService.SaveTokenDetail(params).then((response) => {
                    console.log("response",response);
                    debugger
                    if(response.data=="Success"){
                        this.$router.push({ name: 'AddAccount' });
                    }
                    this.isLoading=false;
                })
             }
        }
    }
})
</script>